.glitch {
  display: block;
  margin-top: 0;
  position: relative;
  color: #ff3d3b; }

.glitch::before,
.glitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #ff3d3b; }

.glitch::before {
  left: 2px;
  text-shadow: -1px 0 #0d2ed1;
  background: black;
  clip: rect(44px, 450px, 56px, 0);
  animation: noise-anim 18s infinite linear alternate-reverse; }

.glitch::after {
  left: -2px;
  text-shadow: -1px 0 #0d2ed1;
  /* important: opaque background masks the original */
  background: black;
  animation: noise-anim 18s infinite linear alternate-reverse; }

@keyframes noise-anim {
  0% {
    clip-path: inset(82% 0 13% 0); }
  5% {
    clip-path: inset(11% 0 3% 0); }
  10% {
    clip-path: inset(14% 0 15% 0); }
  15% {
    clip-path: inset(53% 0 42% 0); }
  20% {
    clip-path: inset(53% 0 20% 0); }
  25% {
    clip-path: inset(64% 0 11% 0); }
  30% {
    clip-path: inset(35% 0 15% 0); }
  35% {
    clip-path: inset(87% 0 14% 0); }
  40% {
    clip-path: inset(93% 0 5% 0); }
  45% {
    clip-path: inset(59% 0 13% 0); }
  50% {
    clip-path: inset(90% 0 1% 0); }
  55% {
    clip-path: inset(77% 0 16% 0); }
  60% {
    clip-path: inset(94% 0 5% 0); }
  65% {
    clip-path: inset(95% 0 6% 0); }
  70% {
    clip-path: inset(57% 0 5% 0); }
  75% {
    clip-path: inset(96% 0 4% 0); }
  80% {
    clip-path: inset(78% 0 21% 0); }
  85% {
    clip-path: inset(33% 0 55% 0); }
  90% {
    clip-path: inset(71% 0 6% 0); }
  95% {
    clip-path: inset(38% 0 29% 0); }
  100% {
    clip-path: inset(18% 0 44% 0); } }
